import React from 'react';

const makeStars = (numOfStars) => {
  const stars = [];
  for (let i = 1; i < 6; i++) {
    if (i <= numOfStars) {
      stars.push(
        <span key={i} className="single__star single__star--lit">
          ‚òÖ
        </span>
      );
    } else {
      stars.push(
        <span key={i} className="single__star single__star--dim">
          ‚òÖ
        </span>
      );
    }
  }
  return stars;
};

export default makeStars;
